import pickerAddress from '../../components/GRZX/wangding-pickerAddress/wangding-pickerAddress.vue' 
export default {
	components:{
		pickerAddress
	},
	data(){	
		return{			
			address:{
				addressID:'',
				receiver:'',
				phoneNum:'',
				district:'请选择 省/市/区 >',
				/* district:'', */
				detailAddress:'',
				postalCode:'',
				default:false
			},
			type:'',
		}
			
	},
	onLoad (options){
		this.type=options.type;
		if(options.type=="edit"){
			this.loadData();
		}
		
	},
	methods:{
		async loadData(){
			var that=this;
			uni.getStorage({
				key:'editAddress',
				success:function(res) {
					that.address.receiver=res.data.receiver;
					that.address.phoneNum=res.data.phoneNum;
					that.address.district=res.data.district;
					that.address.detailAddress=res.data.detailAddress;
					that.address.postalCode=res.data.postalCode;
					that.address.default=res.data.default;
					that.address.addressID=res.data.addressID;
				}
			})		
		},
		formSubmit:function(e){
			var that=this;
			var addList=e.target.value;
			addList.district=this.address.district;
			if(addList.defaultAddress.length==0){
				addList.default=false;
			}else{
				addList.default=true;
			}
			addList.hiddenIndex=0;
			var array=[];
			if(this.type=='edit'){
				//array.push(addList);
				uni.getStorage({
					key:'addressList',
					success(res) {
						for(var i=0;i<res.data.length;i++){
							if(that.address.addressID==res.data[i].addressID){
								array.push(addList);
							}else{
								array.push(res.data[i]);
							}
						}
						uni.setStorage({
							key:'addressList',
							data:array,
						})
					},
					fail() {
						uni.setStorage({
							key:'addressList',
							data:array,
						})
					}
				})
				uni.redirectTo({
					url:'/pages/GRZX/infoList'
				})
			}else if(this.type=='add'){
				var randomNum = ('000000' + Math.floor(Math.random() * 999999)).slice(-6);
				addList.addressID=randomNum;
				array.push(addList);
				uni.getStorage({
					key:'addressList',
					success(res) {
						for(var i=0;i<res.data.length;i++){
							array.push(res.data[i]);
						}
						uni.setStorage({
							key:'addressList',
							data:array,
						})
					},
					fail() {
						uni.setStorage({
							key:'addressList',
							data:array,
						})
					}
				})
				uni.redirectTo({
					url:'/pages/GRZX/infoList'
				})
			}else{
				var randomNum = ('000000' + Math.floor(Math.random() * 999999)).slice(-6);
				addList.addressID=randomNum;
				array.push(addList);
				uni.getStorage({
					key:'addressList',
					success(res) {
						for(var i=0;i<res.data.length;i++){
							array.push(res.data[i]);
						}
						uni.setStorage({
							key:'addressList',
							data:array,
						})
					},
					fail() {
						uni.setStorage({
							key:'addressList',
							data:array,
						})
					}
				})
				uni.navigateBack();
			}
		},
		formReset:function(e){
			this.address.district="请选择 省/市/区 >";
		},
		districtChange:function(e){
			this.address.district=e.data.join(' ');
		}
	}	
}
